* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --color-white: #fff;
}

body {
  flex-direction: column;
  gap: 20px;
  padding: 20px 8px;
  font-weight: 300;
  display: flex;
}

.search-input {
  background: var(--color-white);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  gap: 17px;
  height: 70px;
  padding: 26px;
  transition: box-shadow .3s;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-input:before {
  content: "";
  background: url("Search.ccd3c07b.svg") center / contain no-repeat;
  flex-shrink: 0;
  width: 19px;
  height: 19px;
}

.search-input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search-input:has(:focus) {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  box-shadow: 0 4px 4px #00000040;
}

.search-input--small {
  border-radius: 3px;
  gap: 9px;
  height: 42px;
  padding: 13px;
}

.search-input--small:before {
  width: 11px;
  height: 11px;
}

.search-input--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-input--small:has(:focus) {
  box-shadow: none;
}

.search-input__item {
  color: #3d4e61;
  background: none;
  border: none;
  outline: none;
  flex-grow: 1;
  font-family: Avenir, cursive;
  font-size: 16px;
  font-weight: 300;
  transition: font-weight .3s;
}

.search-input__item::placeholder {
  opacity: 1;
  color: #3d4e61;
}

.search-input__item:focus {
  font-weight: 900;
}

.search-input__item--small {
  font-size: 14px;
}
